.login__bg {
  position: relative;
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week8/login.jpg?raw=true);
  background-position: center center;
  background-size: cover;
  width: 100%;
  min-height: 100vh; }

.location__tag {
  bottom: 24px;
  display: flex;
  align-items: center;
  background: #121212;
  width: 181px; }

.login__container {
  max-width: 360px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }

.memberCheckout__size {
  object-fit: cover;
  width: 100%;
  height: 226px; }
  @media (min-width: 768px) {
    .memberCheckout__size {
      width: 271px;
      height: 186px; } }
  @media (min-width: 992px) {
    .memberCheckout__size {
      width: 255px;
      height: 175px; } }

.profile__photo {
  width: 24px;
  height: 24px;
  object-fit: cover;
  border: 2px solid #6c51e2; }

.personal__photo {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 8px; }

.visa__icon {
  width: 18px;
  height: 14px;
  object-fit: cover; }
